.btnbox {
    display: flex;
    position: relative;
    width: fit-content;
    cursor: pointer;
    animation: 3s btnpattern infinite;
    animation-delay: 2s;
    transition: all .7s linear;
    filter: none;
    @media (max-width: $sm) {
        margin: 0 auto;
    }
    @keyframes btnpattern {
        0% {
            filter: drop-shadow(#F0C687 0rem 0rem 0px);
        }
        50% {
            filter: drop-shadow(#F0C687 0rem 0rem 10px);
        }
        100% {
            filter: drop-shadow(#F0C687 0rem 0rem 0px);
        }
    }
    .center{
        width: 0%;
        transition: all 0.7s linear;
        animation: 3s forwards centeranim;
        animation-delay: .5s;
        overflow: hidden;
        .centertxt{
            color: #000;
            font-size: 14px;
            line-height: 18px;
            font-weight: 600;
            position: absolute;
            top: 50%;
            left: 50%;
            transform: translate(-50%, -50%);
            text-align: center;
            width: 100%;
            opacity: 0;
            transition: all 0.7s linear;
            animation: 1s forwards knowm;
            animation-delay: 1.8s;
            @media (max-width: $sm) {
                font-size: 11px;
                line-height: 15px;
            }
        }
    }
    @keyframes centeranim {
        from {
          width: 0%;
        }
        to {
            width: 100%;
        }
    }
    @keyframes knowm {
        from {
          opacity: 0;
        }
        to {
            opacity: 1;
        }
    }
}
